import React from "react";
import "./../App.css";
import "./../Cards.css";

import welcome from "../static/img/welcome.jpeg";
import ceremonie from "../static/img/ceremonie.jpeg";
import diner from "../static/img/soiree.png";

export default class Cards extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raclettefx: "bgimg",
      raclettebool: false,
      ceremofx: "bgimg",
      ceremobool: false,
      soireefx: "bgimg",
      soireebool: false,
      rowDisplay : "row normal-display",
      racletteShow: false,
      racletteStat: "raclette-content raclette-hide",
      racletteTitle : "bgbanner-text",
      racletteBtn: "",
      racletteBtnTitle: "AFFICHER",
      racletteBg: "",
      ceremoShow: false, 
      ceremoStat: "raclette-content raclette-hide",
      ceremoTitle: "bgbanner-text",
      ceremoBtn: "",
      ceremoBtnTitle: "AFFICHER",
      ceremoBg: "",
      soireeShow: false, 
      soireeStat: "raclette-content raclette-hide",
      soireeTitle: "bgbanner-text",
      soireeBtn: "",
      soireeBtnTitle: "AFFICHER",
      soireeBg: "",
    };
    this.overRaclette = this.overRaclette.bind(this);
    this.overCeremo = this.overCeremo.bind(this);
    this.overSoiree = this.overSoiree.bind(this);
    this.showInfoRaclette = this.showInfoRaclette.bind(this);
    this.showInfoCere = this.showInfoCere.bind(this);
    this.showInfoSoiree = this.showInfoSoiree.bind(this);
  }
  overRaclette() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.raclettebool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
    }
    this.setState({
      raclettefx: val1,
      raclettebool: val2,
    });
    if (val2 === true && this.state.racletteShow === true) {
      this.setState({
        racletteBg: ""
      });
    }
    else if (val2 === false && this.state.racletteShow === false) {
      this.setState({
        racletteBg: ""
      });
    }
    else if ((val2 === false && this.state.racletteShow === true)){
      this.setState({
        racletteBg: "active"
      });
    }
  }
  overCeremo() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.ceremobool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
    }
    this.setState({
      ceremofx: val1,
      ceremobool: val2,
    });
    if (val2 === true && this.state.ceremoShow === true) {
      this.setState({
        ceremoBg: ""
      });
    }
    else if (val2 === false && this.state.ceremoShow === false) {
      this.setState({
        ceremoBg: ""
      });
    }
    else if ((val2 === false && this.state.ceremoShow === true)){
      this.setState({
        ceremoBg: "active"
      });
    }
  }
  overSoiree() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.soireebool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
    }
    this.setState({
      soireefx: val1,
      soireebool: val2,
    });
    if (val2 === true && this.state.soireeShow === true) {
      this.setState({
        soireeBg: ""
      });
    }
    else if (val2 === false && this.state.soireeShow === false) {
      this.setState({
        soireeBg: ""
      });
    }
    else if ((val2 === false && this.state.soireeShow === true)){
      this.setState({
        soireeBg: "active"
      });
    }
  }
  showInfoRaclette() {
    if (this.state.racletteShow === false) {
      this.setState({
        racletteShow: true,
        racletteTitle: "bgbanner-text rmoveup",
        racletteBtn: "rmovedown",
        racletteBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        racletteStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        racletteShow: false,
        racletteStat: "raclette-content raclette-hide",
        racletteTitle: "bgbanner-text",
        racletteBtn: "",
        racletteBtnTitle: "AFFICHER",
      })
    }
    

  }
  showInfoCere() {
    if (this.state.ceremoShow === false) {
      this.setState({
        ceremoShow: true,
        ceremoTitle: "bgbanner-text rmoveup",
        ceremoBtn: "rmovedown",
        ceremoBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        ceremoStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        ceremoShow: false,
        ceremoStat: "raclette-content raclette-hide",
        ceremoTitle: "bgbanner-text",
        ceremoBtn: "",
        ceremoBtnTitle: "AFFICHER",
      })
    }
  }
  showInfoSoiree() {
    if (this.state.soireeShow === false) {
      this.setState({
        soireeShow: true,
        soireeTitle: "bgbanner-text rmoveup",
        soireeBtn: "rmovedown",
        soireeBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        soireeStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        soireeShow: false,
        soireeStat: "raclette-content raclette-hide",
        soireeTitle: "bgbanner-text",
        soireeBtn: "",
        soireeBtnTitle: "AFFICHER",
      })
    }
  }
  render() {
    return (
      <div>
      <div className="row desktop">
        <div className="col-sm wedding-card mr-4 mb-4">
          <div className="centerBox" onMouseOver={this.overRaclette} onClick={this.showInfoRaclette} onMouseOut={this.overRaclette}>
            <img className={`${this.state.raclettefx} ${this.state.racletteBg}`}
            src={welcome} 
            alt={welcome}></img>
            <div className="categoryWrapper">
            <div className={`${this.state.racletteStat}`}>
              <div className="date-title">VENDREDI 18 MARS 2022</div>
              <p>A PARTIR DE 18H00</p>
              <p className="location-text">AU RESTAURANT CHETZERON</p>
              <div className="normal-text">
              RACLETTE PARTY A CHETZERON 
                  (2112 M D'ALTITUDE)
                  
              </div>
              <p className="normal-text">LES TELECABINES FONCTIONNERONT EXCEPTIONNELLEMENT DE 18 A 19 HEURES PRECISES</p>
              <p className="normal-text bold">ATTENTION : LES TELECABINES S'ARRETENT A 19H. AU DELA, IL NE SERA PLUS POSSIBLE D'ACCEDER A CHETZERON</p>
              <p className="normal-text">A L'ARRIVEE A CRY D'ER : TRANSFERT EN CHENILLETTES JUSQU'A CHETZERON (PREVOIR UNE SECONDE PAIRE DE CHAUSSURES POUR LES ADEPTES DES TALONS).</p>
              <p className="small-text">
                {/* <img className="dresscode" src={mdress} alt={mdress}></img>
                <img className="dresscode" src={fdress} alt={fdress}></img> */}
                DRESSCODE : SPORT CHIC</p>
            </div>
              <h5 className={this.state.racletteTitle}>WELCOME DINNER</h5>
              <button className={this.state.racletteBtn}>
                <span>
                  <span>
                    <span data-attr-span={this.state.racletteBtnTitle}>
                      {this.state.racletteBtnTitle}
                    </span>
                  </span>
                </span>
              </button>
            </div>
          </div>
        </div>
        <div className="col-sm wedding-card mr-4 mb-4">
          <div className="centerBox" onMouseOver={this.overCeremo} onClick={this.showInfoCere} onMouseOut={this.overCeremo}>
            <img className={`${this.state.ceremofx} ${this.state.ceremoBg}`}
              src={ceremonie}  
              alt={ceremonie} ></img>
              <div className="categoryWrapper">
              <div className={`${this.state.ceremoStat} adjust`}>
                <p className="date-title">SAMEDI 19 MARS 2022 A 15H00</p>
                <p className="location-text">A L'EGLISE DU SACRE-COEUR DE MONTANA</p>
                <div className="normal-text">
                    <br></br><br></br><br></br><br></br>
                </div>
              </div>
                <h5 className={this.state.ceremoTitle}>CEREMONIE RELIGIEUSE</h5>
                <button className={this.state.ceremoBtn}>
                  <span>
                    <span>
                      <span data-attr-span={this.state.ceremoBtnTitle}>
                        {this.state.ceremoBtnTitle}
                      </span>
                    </span>
                  </span>
                </button>
              </div>
          </div>
        </div>
        <div className="col-sm wedding-card mr-4 mb-4">
          <div className="centerBox" onMouseOver={this.overSoiree} onClick={this.showInfoSoiree} onMouseOut={this.overSoiree}>
          <img className={`${this.state.soireefx} ${this.state.soireeBg}`}
              src={diner}  
              alt={diner} ></img>
              <div className="categoryWrapper">
              <div className={`${this.state.soireeStat} adjust`}>
                <p className="date-title">SAMEDI 19 MARS 2022</p>
                <p className="location-text">AU GUARDA GOLD HOTEL - CRANS MONTANA</p>
                <p className="normal-text">
                    COCKTAIL A PARTIR DE 19H SUIVI D'UN DINER ASSIS
                </p>
                <p className="small-text">DRESSCODE : TENUE HABILLEE</p>
              </div>
                <h5 className={this.state.soireeTitle}>SOIREE</h5>
                <button className={this.state.soireeBtn}>
                  <span>
                    <span>
                      <span data-attr-span={this.state.soireeBtnTitle}>
                        {this.state.soireeBtnTitle}
                      </span>
                    </span>
                  </span>
                </button>
              </div>
          </div>
        </div>
      </div>
        <div className="row mobile ipad">
          <div className="col-sm wedding-card mr-4 mb-4">
            <img className="prog-img-for-small-screen" src={welcome} alt={welcome}></img>
          </div>
          <div className="col-sm wedding-card mr-4 mb-4">
            <div className="date-title">VENDREDI 18 MARS 2022</div>
            <div className="date-title">A PARTIR DE 18H00</div>
            <p className="location-text">AU RESTAURANT CHETZERON</p>
            <div className="container-for-info">
              <div className="normal-text">
              RACLETTE PARTY A CHETZERON 
                  (2112 M D'ALTITUDE)
                  
              </div>
              <p className="normal-text">LES TELECABINES FONCTIONNERONT EXCEPTIONNELLEMENT DE 18 A 19 HEURES PRECISES</p>
              <p className="normal-text bold">ATTENTION : LES TELECABINES S'ARRETENT A 19H. AU DELA, IL NE SERA PLUS POSSIBLE D'ACCEDER A CHETZERON</p>
              <p className="normal-text">A L'ARRIVEE A CRY D'ER : TRANSFERT EN CHENILLETTES JUSQU'A CHETZERON (PREVOIR UNE SECONDE PAIRE DE CHAUSSURES POUR LES ADEPTES DES TALONS).</p>
              <p className="small-text">
                {/* <img className="dresscode" src={mdress} alt={mdress}></img>
                <img className="dresscode" src={fdress} alt={fdress}></img> */}
                DRESSCODE : SPORT CHIC</p>
            </div>
          </div>
          <div className="col-sm wedding-card mr-4 mb-4">
            <img className="prog-img-for-small-screen ceremonie" src={ceremonie} alt={ceremonie}></img>
          </div>
          <div className="col-sm wedding-card mr-4 mb-4">
            <p className="date-title">SAMEDI 19 MARS 2022 A 15H00</p>
            <p className="location-text">A L'EGLISE DU SACRE-COEUR DE MONTANA</p>
          </div>
          <div className="col-sm wedding-card mr-4 mb-4">
            <img className="prog-img-for-small-screen diner" src={diner} alt={diner}></img>
          </div>
          <div className="col-sm wedding-card mr-4 mb-4">
            <p className="date-title">SAMEDI 19 MARS 2022</p>
            <p className="location-text">AU GUARDA GOLD HOTEL - CRANS MONTANA</p>
            <div className="container-for-info">
            <p className="normal-text">
                COCKTAIL A PARTIR DE 19H SUIVI D'UN DINER ASSIS
            </p>
            <p className="small-text">DRESSCODE : TENUE HABILLEE</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}