import React from "react";
import "../parallax.css";
import "../App.css";

import map from "../static/img/map.gif";

export default class parallax extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      guestList: [],
      loading: true,
    };
  }
  
  render() {
    return(
      <div className="lg-screen-only">
        <div className="crans-map-container">
          <img className="crans-map-img" src={map} alt={map}></img>
        </div>
      </div>
    )
  }
}