import "./App.css";

import ScrollAnimation from 'react-animate-on-scroll';
// Carousel for email banner
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";
import Loader from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
// Menu functions

// Imported apps
import NavBar from "./miniapps/navbar.js";
import Cards from "./miniapps/programmation.js";
//import Programme from "./miniapps/programme.js";
import AttendFormR from "./miniapps/form2.js";
import ContactPopover from "./miniapps/footer.js";
import Album from "./miniapps/album.js";
//import Video from "./miniapps/video.js";
import Logistic from "./miniapps/transport.js";
import Hotels from "./miniapps/hotels.js";
import Parallax from "./miniapps/parallax.js";
import Map from "./miniapps/cransmap.js";

import React from "react";

//import media img/movie
import coucher from "./static/img/contact.jpg";

// Main WEBSITE
export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      guestList: [],
      loading: true,
      spinnercss: "spinner",
      activateArrow: "arrowdiv",
      mouseonArr: false,
      isHide: false,
    };
    this.hoverArr = this.hoverArr.bind(this);
  }
  
  componentDidMount() {
    this.wait(4000); // 2000
    this.setState({});
    window.scrollTo(0, 0);
    window.addEventListener('scroll', this.hideBar);
  }
  componentWillUnmount(){
    window.removeEventListener('scroll', this.hideBar);
  }
  hideBar = () => {
    const { isHide } = this.state

    window.scrollY > this.prev ?
    !isHide && this.setState({ isHide: true })
    :
    isHide && this.setState({ isHide: false });

    this.prev = window.scrollY;
  }
  hoverArr() {
    if (this.state.mouseonArr === false) {
      this.setState({
      activateArrow: "arrowdiv arrowfx",
      mouseonArr: true,
    })
    }
    else {
      this.setState({
        activateArrow: "arrowdiv",
        mouseonArr: false,
      })
    }
  }
  wait = async (milliseconds = 4000) => { // 2000
    await this.sleep(milliseconds);
    this.setState({
      loading: false,
      spinnercss: "spinner move-spinner",
    });
  };
  sleep = (milliseconds) => {
    return new Promise((resolve) => setTimeout(resolve, milliseconds));
  };


  render() {
    const classHide = this.state.isHide ? 'hide' : '';
    return (
      <div>
        <div className={this.state.spinnercss}>
          <div>BENJAMIN</div>
          <Loader
          type="Hearts"
          color="#517697"
          height={150}
          width={150}
          timeout={4100} //3 secs
          />
          <div>CAMILLE</div>
        </div>
        {/* <div className="up">UP</div> */}
        <NavBar></NavBar>
        <div className="container-fluid">
          {/* main container */}
          <div className="main-container">
          {/* CAROUSEL MAIN MENU */} 
            <ScrollAnimation initiallyVisible={true} animateIn="fadeIn">
                <div className="carousel-img-container" id="home">
                  <Parallax></Parallax>
                </div>
            </ScrollAnimation>
            
            {/* PROGRAMME */}
            <div className="programme">
            <p className="programme-title">PROGRAMME</p>
              <br />
              <ScrollAnimation animateIn="fadeIn">
                <Cards></Cards>
                <Map></Map>
              </ScrollAnimation>
            </div>

            {/* TRANSPORTS */}
            <div className="logistic">
            <p className="logistic-title">TRANSPORTS</p>
              <br />
              <ScrollAnimation animateIn="fadeIn">
                <Logistic></Logistic>
              </ScrollAnimation>
            </div>
            {/* TRANSPORTS */}
            <div className="hotels">
            <p className="logistic-title">HOTELS</p>
              <br />
              <ScrollAnimation animateIn="fadeIn">
                <Hotels></Hotels>
              </ScrollAnimation>
            </div>

            {/* VIDEO OR MEDIA */}
        
            <div id="album" className="album">
            <p className="album-title">NOS SOUVENIRS</p>
            <ScrollAnimation animateIn="fadeIn">
              <Album></Album>
            </ScrollAnimation>
            </div>

            {/* RVSP */}
            <div id="rsvp" className="invit">
            <p className="contact-title">INVITATION</p>
            <p className="spacer-title-frame"></p>
              <div className="rsvp-carousel">
                <div className="span-text"></div>
                <ScrollAnimation animateIn="fadeIn">
                  <AttendFormR></AttendFormR>
                </ScrollAnimation>
              </div>
            </div>
            {/* CONTACT */}
            <div id="contact" className="contact">
                <div
                  className="contact-frame"
                  style={{ paddingTop: "120px" }}
                >
                  <div className="contact-oncarousel">
                    <div className="contact-frame">
                      <a
                        className="contact-link"
                        href="mailto:contact@benjamin-camille.com"
                      >
                        CONTACT@BENJAMIN-CAMILLE.COM
                      </a>
                    </div>
                    <p></p>
                    <p></p>
                    <div className="space-div-contact"></div>
                  </div>
                  <ScrollAnimation animateIn="fadeIn">
                    <Carousel
                    showArrows={true}
                    autoPlay={true}
                    infiniteLoop={true}
                    centerMode={false}
                    showThumbs={false}
                    showIndicators={false}
                    stopOnHover={false}
                    showStatus={false}
                    height={500}
                    >
                    <div>
                      <img
                        src={coucher}
                        alt={coucher}
                        className="merry-go-round-contact-img"
                        style={{ filter: "brightness(120%)" }}
                      />
                    </div>
                    </Carousel>
                  </ScrollAnimation>
                  
                </div>
            </div>
          </div>
          {/* FIN CONTAINER */}
          <div className="footer-container">
            <div className="row timer-row">
              <div className="footer-info">
                <ContactPopover></ContactPopover>
                <p></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
