import React from "react";
import "./../App.css";
import "./../Cards.css";

// import five from "../static/img/guarda.jpg";
// import four from "../static/img/etrier.jpg";
// import three from "../static/img/3stars.jpg";
import flocon from "../static/img/flocon2.png";

export default class Cards extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raclettefx: "bgimg2",
      raclettebool: false,
      ceremofx: "bgimg2",
      ceremobool: false,
      soireefx: "bgimg2",
      soireebool: false,
      showFive : "",
      showFour : "",
      showThree : "",
      css3: "",
      css4: "",
      css5: "",
      showDefault: "show",
    };
    this.overRaclette = this.overRaclette.bind(this);
    this.overCeremo = this.overCeremo.bind(this);
    this.overSoiree = this.overSoiree.bind(this);
    this.showInfo = this.showInfo.bind(this);
  }
  overRaclette() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.raclettebool === false) {
      // create hover effect
      val1 = "bgimg2 active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg2";
      val2 = false;
      // if raclette is show on then
    }
    this.setState({
      raclettefx: val1,
      raclettebool: val2,
    });
    if (val2 === true && this.state.racletteShow === true) {
      this.setState({
        racletteBg: ""
      });
    }
    else if (val2 === false && this.state.racletteShow === false) {
      this.setState({
        racletteBg: ""
      });
    }
    else if ((val2 === false && this.state.racletteShow === true)){
      this.setState({
        racletteBg: "active"
      });
    }
  }
  overCeremo() {
    var val1 = ""
    var val2 = ""
    if (this.state.ceremobool === false) {
      val1 = "bgimg2 active";
      val2 = true;
    }
    else {
      val1 = "bgimg2"
      val2 = false
    }
    this.setState({
      ceremofx: val1,
      ceremobool: val2,
    });
  }
  overSoiree() {
    var val1 = ""
    var val2 = ""
    if (this.state.soireebool === false) {
      val1 = "bgimg2 active";
      val2 = true;
    }
    else {
      val1 = "bgimg2"
      val2 = false
    }
    this.setState({
      soireefx: val1,
      soireebool: val2,
    });
  }
  showInfo(x) {
    var five = "test"
    var four = ""
    var three = ""
    var css3 = ""
    var css4 = ""
    var css5 = ""
    if (x === "five") {
      five = "show";
      four = "";
      three = "";
      css3 = "";
      css4 = "";
      css5 = "bluestars";
    }
    else if (x === "four") {
      five = "";
      four = "show";
      three = "";
      css3 = "";
      css4 = "bluestars";
      css5 = "";
    }
    else if (x === "three") {
      five = "";
      four = "";
      three = "show";
      css3 = "bluestars";
      css4 = "";
      css5 = "";
    }
    this.setState({
      showFive: five,
      showFour: four,
      showThree: three,
      css3: css3,
      css4: css4,
      css5: css5,
      showDefault: "",
    })
  }
  render() {
    return (
    <div>
        <div className="row hotels-row">
                <div className="col hotels-col mr-4 mb-4">
          <div className="centerBox" onMouseOver={this.overRaclette} onClick={() => this.showInfo("five")} onMouseOut={this.overRaclette}>
            {/* <img className={`${this.state.raclettefx} ${this.state.css5}`}
            src={five} 
            alt={five}></img> */}
            <div className="categoryWrapper desktop">
            <h4 className={`bgbanner-text ${this.state.css5}`}><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img></h4>
            </div>
            <div className="mobile ipad">
            <h4 className={`bgbanner-text ${this.state.css5}`}>5 <img src={flocon} alt={flocon}></img></h4>
            </div>
          </div>
        </div>
      <div className="col hotels-col mr-4 mb-4">
        <div className="centerBox" onMouseOver={this.overCeremo} onClick={() => this.showInfo("four")} onMouseOut={this.overCeremo}>
          {/* <img className={`${this.state.ceremofx} ${this.state.css4}`}
            src={four}  
            alt={four} ></img> */}
            <div className="categoryWrapper desktop">
              <h4 className={`bgbanner-text ${this.state.css4}`}><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img></h4>
            </div>
            <div className="mobile ipad">
              <h4 className={`bgbanner-text ${this.state.css4}`}> 4 <img src={flocon} alt={flocon}></img></h4>
            </div>
        </div>
      </div>
      <div className="col hotels-col mr-4 mb-4">
      <div className="centerBox" onMouseOver={this.overSoiree} onClick={() => this.showInfo("three")} onMouseOut={this.overSoiree}>
      {/* <img className={`${this.state.soireefx} ${this.state.css3}`}
          src={three}
          alt={three}></img> */}
          <div className="categoryWrapper desktop">
              <h4 className={`bgbanner-text ${this.state.css3}`}><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img></h4> 
          </div>
          <div className="mobile ipad">
              <h4 className={`bgbanner-text ${this.state.css3}`}> 3 <img src={flocon} alt={flocon}></img></h4> 
          </div>
        </div>
      </div>
    </div>
    <div className="row info-hotel">
    <div className={`fivestars ${this.state.showDefault}`}>
        <div className="date-title">NOUS VOUS LAISSONS LE SOIN DE CHOISIR L'HOTEL DANS LEQUEL VOUS SOUHAITEZ SEJOURNER</div>
        <br></br>
      </div>
      <div className={`fivestars ${this.state.showFive}`}>
        <div className="date-title"><span className="link-hotel"><a href="https://guardagolf.com/" target="_blank" rel="noreferrer noopener" 
        >GUARDA HOTEL</a></span> <span className="indication">(LIEU DU MARIAGE)</span><br></br><span className="indication">NOUS CONTACTER DIRECTEMENT</span></div>
        <br></br>
        <p className="date-title"><span className="link-hotel"><a href="https://pasdelours.ch/fr/" target="_blank" rel="noreferrer noopener" >LE PAS DE L'OURS</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="https://www.cransambassador.ch/fr" target="_blank" rel="noreferrer noopener" >L'AMBASSADOR</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="http://www.hotel-royal.ch/" target="_blank" rel="noreferrer noopener" >L'HOTEL ROYAL</a></span></p>
      </div>
      <div className={`fourstars ${this.state.showFour}`}>
        <p className="date-title"><span className="link-hotel"><a href="https://www.hoteletrier.ch/fr/" target="_blank" rel="noreferrer noopener" 
        >HOTEL DE L'ETRIER</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="https://www.helvetia-intergolf.ch/fr/" target="_blank" rel="noreferrer noopener" 
        >HOTEL HELVETIA INTERGOLF</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="https://art-vivre.ch/" target="_blank" rel="noreferrer noopener" 
        >HOTEL ART DE VIVRE</a></span></p>
      </div>
      <div className={`threestars ${this.state.showThree}`}>
        <p className="date-title"><span className="link-hotel"><a href="https://montpaisible.ch/" target="_blank" rel="noreferrer noopener" 
        >LE MONT PAISIBLE</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="https://www.hotel-splendide.com/" target="_blank" rel="noreferrer noopener" 
        >HOTEL SPLENDIDE</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="https://www.boutique-hotel-du-lac.ch/" target="_blank" rel="noreferrer noopener" 
        >HOTEL DU LAC</a></span></p>
        <p className="date-title"><span className="link-hotel"><a href="https://lhotelcentral.ch/" target="_blank" rel="noreferrer noopener" 
        >HOTEL CENTRAL</a></span></p>
      </div>
    </div>
    
  </div>
    
);
  }
}