import React from "react";
import "./../App.css";
import "./../Cards.css";

export default class Logistic extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      soireebool: false,
    };
  }

  render() {
    return (
      <div className="row logistic-row">
        <div className="logistic-table">
          <div className="row logistic-padding">
            <div className="col logistic-col">
              <p className="head-text">PROVENANCE DE PARIS</p>
              <div className="normal-text"><i className="fas fa-train"></i> GARE DE LYON <i className="fas fa-long-arrow-alt-right"></i> SION : DEPART 10h18 - ARRIVEE 15h56</div>
              <div className="normal-text"><i className="fas fa-plane"></i> ORLY <i className="fas fa-long-arrow-alt-right"></i> GENEVE (EASYJET) : DEPART 10h30 - ARRIVEE 11h35</div>
            </div>
            <div className="col logistic-col">
              <p className="head-text">AUTRES PROVENANCES</p>
              <div className="normal-text">NOUS VOUS LAISSONS LE SOIN DE CONSULTER LES PROPOSITIONS DE VOS COMPAGNIES AERIENNES PREFEREES POUR LA DESTINATION DE GENEVE</div>
            </div>
          </div>
          <div className="row logistic-bottom">
            <div className="col head-text"><p> <i className="fas fa-taxi taxi"></i> RESERVEZ UN TAXI DEPUIS SION OU <i className="fas fa-car"></i> LOUEZ UNE VOITURE DEPUIS GENEVE</p>
              <p><a className="normal-link" href="https://www.crans-montana.ch/fr/taxi/" target="_blank" rel="noreferrer noopener">RESERVEZ VOTRE TAXI ICI</a></p>
            </div>
          </div>
          <div className="row logistic-bottom">
            <div className="col bold"> <p className="normal-text"><i className="fas fa-exclamation-circle"></i> IL FAUT IMPERATIVEMENT ETRE A CRANS-MONTANA LE 18 MARS AVANT 16h30</p></div>
          </div>
        </div>
    </div>
);
  }
}